<template>
  <div class="resource-category-list">
    <el-card
      shadow="never">
      <!-- 添加资源分类 -->
      <div slot="header">
        <el-button
          size="mini"
          @click="handleAddResCategory">添加</el-button>
      </div>
      <!-- 资源数据表格 -->
      <el-table
        :data="resCategories"
        v-loading="isLoading"
        border
        style="width: 100%">
        <el-table-column
          type="index"
          label="编号"
          width="80"
          align="center" />
        <el-table-column
          prop="name"
          label="名称"
          align="center" />
        <el-table-column
          label="创建时间"
          align="center">
          <template slot-scope="scope">
            {{ scope.row.createdTime | formatDate }}
          </template>
        </el-table-column>
        <el-table-column
          prop="sort"
          label="排序"
          align="center" />
        <el-table-column
          label="操作"
          align="center" >
          <template slot-scope="scope">
            <el-button
              type="text"
              @click="handleEditResCategory(scope.row)">编辑</el-button>
            <el-button
              type="text"
              @click="handleDeleteResCategory(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加/编辑资源分类对话框 -->
    <el-dialog
      v-if="resCategoryDialogVisible"
      :visible="resCategoryDialogVisible"
      :title="`${ isEditResCategory ? '编辑' : '添加' }资源分类`"
      width="30%">
      <res-category-create-or-edit
        :is-edit="isEditResCategory"
        :category="resCategory"
        @success="handleDialogSuccess"
        @cancel="handleDialogCancel"></res-category-create-or-edit>
    </el-dialog>
  </div>
</template>

<script>
import {
  getAllResourceCategory,
  deleteResourceCategory
} from '@/services/resource'
import {
  toStandardTimeFormat
} from '@/utils/format'
import ResCategoryCreateOrEdit from './res-category-create-or-edit'

export default {
  name: 'ResourceCategoryList',
  components: {
    ResCategoryCreateOrEdit
  },
  data () {
    return {
      resCategories: [],
      resCategoryQuery: {
        size: 10,
        current: 1
      },
      resCategoryTotalCount: 0,
      isLoading: false,
      resCategoryDialogVisible: false,
      isEditResCategory: false,
      resCategory: ''
    }
  },
  created () {
    this.loadResCategories()
  },
  methods: {
    async loadResCategories () {
      this.isLoading = true
      const { data } = await getAllResourceCategory()
      if (data.code === '000000') {
        this.resCategories = data.data
      }
      this.isLoading = false
    },
    handleAddResCategory () {
      this.resCategoryDialogVisible = true
      this.isEditResCategory = false
      this.resCategory = null
    },
    handleEditResCategory (category) {
      this.resCategoryDialogVisible = true
      this.isEditResCategory = true
      this.resCategory = category
    },
    handleDeleteResCategory (category) {
      this.$confirm('确认删除吗？', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data } = await deleteResourceCategory(category.id)
        if (data.code === '000000') {
          this.$message.success('删除成功')
          this.loadResCategories()
        }
      }).catch(() => {
        this.$message.info('取消删除')
      })
    },
    handleDialogSuccess () {
      this.resCategoryDialogVisible = false
      this.loadResCategories()
    },
    handleDialogCancel () {
      this.resCategoryDialogVisible = false
    }
  },
  filters: {
    formatDate (date) {
      return toStandardTimeFormat(date)
    }
  }
}
</script>

