<template>
  <div class="resource-category-create-or-edit">
    <el-form>
      <el-form-item
        label="名称">
        <el-input
          v-model="categoryForm.name"></el-input>
      </el-form-item>
      <el-form-item
        label="排序">
        <el-input
          v-model="categoryForm.sort"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          size="mini"
          @click="handleCancel">取消</el-button>
        <el-button
          size="mini"
          type="primary"
          @click="handleSubmit">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  createOrUpdateCategory
} from '@/services/resource'

export default {
  name: 'ResCategoryCreateOrEdit',
  props: {
    category: {
      type: Object
    }
  },
  data () {
    return {
      categoryForm: this.category || {}
    }
  },
  methods: {
    async handleSubmit () {
      const { data } = await createOrUpdateCategory(this.categoryForm)
      if (data.code === '000000') {
        this.$message.success('提交成功')
      }
      this.$emit('success')
    },
    handleCancel () {
      this.$emit('cancel')
    }
  }
}
</script>

